import React, { useState, useCallback, useEffect, useContext } from 'react';
import AppContext from '../../../contexts/AppContext';
import ConfirmInfo from '../../confirminfo/ConfirmInfo';
import SacomOtpRequest from './SacomOtpRequest';
import SacomOtpValidate from './SacomOtpValidate';

const SacomValidation = ({transactionId, onSectionStatusChanged}) => {

  const SRCSDK_VIETNAM = window.SRCSDK_VIETNAM;
  const {srcClientId, srcDpaId, paymentBrandName,dpaTransactionOptions} = useContext(AppContext);

  useEffect(() => {
    console.log('SacomValidation loaded. transactionId:' + transactionId);
    var initParams = {
      "srcClientId":  srcClientId,
      "srciTransactionId": transactionId,
      "srcDpaId":   srcDpaId,
      "paymentBrandName": 'sacombank',
    }

  console.log(JSON.stringify(initParams));
  SRCSDK_VIETNAM.init(initParams)
  .catch(function(err){
    console.log(JSON.stringify(err));
    console.log("sdk init fail");
  })

    
  }, []);

  const [otpRequestState, setOtpRequestState] = useState('request_idle');        // 'request_idle', 'request_ok'
  const [otpValidationState, setOtpValidationState] = useState('validate_idle'); // 'validate_idle', 'validate_ok'

  const onOtpRequestEventFired = useCallback(function (event) {
    switch(event.type) {
      case 'onFail':
        setOtpRequestState('request_idle');
        setOtpValidationState('validate_idle');
        return;
      // case 'onRequest':
      //   setOtpRequestState('request_idle');
      //   setOtpValidationState('validate_idle');
      //   return;  
      case 'onSucess':
        console.log(event);
        setOtpRequestState('request_ok');
        setOtpValidationState('validate_idle');
        return;
    }
    // setOtpRequestState(state);
  }, []);
/**
 * event = {
 *   type: "",
 *   result: {
 *     otpRequestSessionId: ""
 *   }
 * 
 * }
 */
  const onOtpValidateEventFired = useCallback(function (event) {
    console.log(event);
    switch(event.type) {
      case 'onFail':
        setOtpRequestState('request_idle');
        setOtpValidationState('validate_idle');
        return;
      case 'onTimeout':
        setOtpRequestState('request_idle');
        setOtpValidationState('validate_idle');
        return;
      case 'onSucess':
        setOtpRequestState('request_ok');
        setOtpValidationState('validate_ok');      

        onSectionStatusChanged({ status: 'confirm', result: {srcCorrelationId: event.result.srcCorrelationId} });
        return;
    }
    // setOtpRequestState(state);
  }, []);



  //SacomValidation화면에서는 각 단계별 화면 컨트롤을 진행한다
  return (
    <>
    {console.log(otpRequestState)}
    {console.log(otpValidationState)}

      {otpValidationState === 'validate_ok' ? <ConfirmInfo />: 
      (
        <>
          <SacomOtpRequest state={otpRequestState} onEventFired={onOtpRequestEventFired}/>
          <div className="mt15" />
          {otpRequestState === 'request_ok' ? <SacomOtpValidate state={otpValidationState} onEventFired={onOtpValidateEventFired}/> : ''}
          <div className="mt15" />
        </>
      )}
    </>
  );
};

export default SacomValidation;
