import React, { useState, useEffect } from 'react';
import './Timer.scss';

const Timer = ({onTimeout}) => {
  const [seconds, setSeconds] = useState(5); //Timer 세팅 값 
  // const [isActive, setIsActive] = useState(true);

  useEffect(() => {
    let interval = null;

    if (seconds > 0) {
      interval = setInterval(() => {
        setSeconds(() => seconds - 1);
      }, 1000);
    } else if (seconds === 0) {
      clearInterval(interval);
      onTimeout();
    }
    return () => clearInterval(interval);
  }, [seconds]);

  // const onTimeout = () => {
  //   console.log('onTimeout');
  //   onTimeout();
  // };

  return (
    <div className="mt10">
      <span className="timerbtn btn_white_s1">
        <a href="#link" className="ac"> Time : {seconds} sec </a>
      </span>
    </div>
  );
};

Timer.defaultProps = {
  time: '120',
};

export default Timer;
