import React, { useState, useCallback } from 'react';
import ConfirmInfo from '../confirminfo/ConfirmInfo';
import Prompt from '../prompt/Prompt';
import LpbValidation from './lpb/LtbValidation';
import Navigator from './navi/Navigator';
import SacomValidation from './sacom/SacomValidation';
import './Section.scss';

const Section = () => {
  const [paymentApp, setPaymentApp] = useState(''); //available state: '', 'sacom', 'lpb'
  const onPaymentAppChanged = useCallback(function (selectedPaymentApp) {
    setPaymentApp(selectedPaymentApp);
  }, []);

  const [sectionStatus, setSectionStatus] = useState('validation'); //available state: 'validation', 'confirm'
  
  const onSectionStatusChanged = useCallback(function (data) {
    console.log(data);
    setValidationRes(data.result);
    setSectionStatus(data.status);
  }, []);

  const [validationRes, setValidationRes] = useState('');

  const setPromptProps = () => {
    if (sectionStatus === 'validation') {
      return 'cancle';
    } else if (sectionStatus === 'confirm') {
      return 'both';
    }
    return 'both';
  };

  const createUUID = () => {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c){
			var r = Math.random() * 16 | 0, v =c =='x' ? r: (r & 3 | 8 );
		    return v.toString(16);
		});
  }

  const resolveSection = () => {
    switch (sectionStatus) {
      case 'validation':
        return (
          <>
            <div className="Section">
              <Navigator onPaymentAppChanged={onPaymentAppChanged} />
              <div className="mt15" />
              {paymentApp === 'sacom' ? <SacomValidation onSectionStatusChanged={onSectionStatusChanged} transactionId={createUUID()} /> : ''}
              {paymentApp === 'lpb' ?   <LpbValidation onSectionStatusChanged={onSectionStatusChanged} transactionId={createUUID()}/> : ''}
              <div className="mt5" />
            </div>
            <div className="mt10" />
            <Prompt type={setPromptProps()} />
          </>
        );
      case 'confirm':
        return(
        <>
          <div className="Section">
            <ConfirmInfo />
          </div>
          <div className="mt10" />
          <Prompt type={setPromptProps()} />
        </>
        );
      default:
        return;
    }
  };

  return (
    <>
      {resolveSection()}
    </>
  );
};

export default Section;
