import React from 'react';
import CancleButton from './CancleButton';
import ConfirmButton from './ConfirmButton';
import './Prompt.scss';

const Prompt = (props) => {
  const { type } = props; // cancle, confirm, both

  const resolvePrompt = () => {
    console.log(type);
    switch (type) {
      case 'both':
        return (
          <>
            <ConfirmButton />
            <div className="pr10"></div>
            <CancleButton />
          </>
        );
      case 'cancle':
        return <CancleButton />;
      case 'confirm':
        return <ConfirmButton />;
      default:
        return <ConfirmButton />;
    }
  };
  return <div className="btn_wrap">{resolvePrompt()}</div>;
};


Prompt.defaultProps = {
  type: 'cancle',
};

export default Prompt;
