import React, { useEffect } from 'react';
import ConfirmItem from './ConfirmItem';
import './ConfirmItemList.scss';

const ConfirmItemList = () => {
  const confirmItems = window.__VN__;

  const createItem = (title, value) => {
    return {
      title,
      value,
    };
  };
  const createItems = (confirmItems) => {
    let items = [];
    for (let k in confirmItems) {
      if (confirmItems[k] && typeof confirmItems[k] === 'object') {
        for (let j in confirmItems[k]) {
          items.push(createItem(j, confirmItems[k][j]));
        }
      } else {
        items.push(createItem(k, confirmItems[k]));
      }
    }

    return items;
  };

  const items = createItems(confirmItems);

  const itemList = items.map((item) => (
    <div key={item.title}>
      <ConfirmItem title={item.title} value={item.value} />
      <div className="mt15" />
    </div>
  ));

  return <div className='gray_box01 mt10"'>{itemList}</div>;
};

export default ConfirmItemList;
