import React, {useState, useCallback} from 'react';
import StepLabel from '../component/StepLabel';
import './Navigator.scss';
import EwalletChooser from '../component/EwalletChooser';

const Navigator = ({onPaymentAppChanged}) => {

  const stepProps = 'Step 1.';
  const valueProps = 'Choose Payment method(Chọnphương pháp thanh toán)';

  // const [ewallet, setEwallet] = useState('');
  const onSelectEwallet = useCallback(function(selectItem) {
    // setEwallet(selectItem);
    onPaymentAppChanged(selectItem);
  }, []);
  
  return (
    <>
      <StepLabel step={stepProps} value={valueProps} />
      <EwalletChooser onSelectItem={onSelectEwallet} />
    </>
  );
};

export default Navigator;
