import React, {useState, useCallback, useEffect} from 'react';
import InputDetail from '../component/InputDetail';
import StepLabel from '../component/StepLabel';

const SacomOtpRequest = ( props ) => {
  const stepProps2 = 'Step 2.';
  const valueProps2 ='Enter Mobile Phone Number and Click request(Nhập số điện thoại và Nhấn nút xác nhận)';

  const { state, onEventFired } = props; //상위컴포넌트로부터 props로 내려받은 컴포넌트의 상태: [request_idle, request_ok] 

  const [disabled, setDisabled] = useState(false);

  useEffect(() => {
    // console.log("otp request useEffect " + state);
    if(state === 'request_idle'){
      // console.log("disable false")
      setDisabled(false);
    } else if(state === 'request_ok') {
      // console.log("disable true")
      setDisabled(true);
    }
  },[state]);


  const onOtpRequested = useCallback (function(reqData) {
    // setReqVal(reqVal);
    console.log(reqData);
    // onEventFired('onRequest');

    //TODO  
    //step1. SRCSDK를 이용해서  sdk 호출
    var optRequestIn = {
      "consumerIdentity": {
        "identityProvider" : "SRC",
        "identityType" : "MOBILE_PHONE_NUMBER",
        "identityValue" : reqData
      }
  };
  window.SRCSDK_VIETNAM.otpRequest(optRequestIn)
  .then(function(data){
    console.log("sucess::"+data);
  })
  .catch(function(err){
    console.log("fail::"+JSON.stringify(err));
  })



    //step2. 호출 결과를 상위 컴포넌트에게 전달
    // onOtpRequestStateChanged('request_' + 'ok');
    onEventFired({type: "onSucess", result: {otpRequestSessionId: "temp"}});
    // setDisabled(true);
  }, []);


  

  return (
    <div>
      <StepLabel step={stepProps2} value={valueProps2} />
      <InputDetail disable={disabled} placeholder={'+84 1234 5679'} onRequested={onOtpRequested} />
    </div>
  );
};

export default SacomOtpRequest;
