import React, {useState} from 'react';
import './UserGuide.scss';
// import '../layout/AppLayout.scss'

const UserGuide = () => {

  const [guideState, setGuideState] = useState('close');

  const onClose = () => {
	document.getElementById('guide').style.display ='none';
  };

  const onOpen = () => {
	document.getElementById('guide').style.display ='block';
  };

  const onClick = () => {
	  if(guideState === 'close') {
		setGuideState('open');
		onOpen();
	  } else  {
		setGuideState('close');
		onClose();
	  }
  };

  return (
        <div id="UserGuide" >
			<a onClick={onClick} href="#link">User Guide (Hướng dẫn sử dụng)</a>
			<div id='guide' className="cont"  style={ { display: 'none' } }>
				<ul className="bu01">
					<li>Payment Method (Phương pháp thanh toán)<br />- Sacombank Pay, ViViet (LienVietPostBank)</li>
					<li>Payment Process (Các bước Thực hiện thanh toán)</li>
				</ul>
				<div className="step_guide">
					<ul className="list">
						<li><p className="bg01">Step 1. Choose payment method (Chọn phương pháp thanh toán)</p></li>
						<li><p className="bg02">Step 2. Enter Mobile Phone Number and Click Request <br />
						(Nhập số điện thoại và Nhấn nút xác nhận)</p></li>
						<li><p className="bg03">Step 3. Check OTP Number through App push after log-in Payment method’s App<br />
						(Kiểm tra số OTP thông qua ứng dụng sau khi đăng nhập)</p></li>
						<li><p className="bg04">Step 4. Enter OTP Number<br /> (Nhập mã OTP)</p></li>
						<li><p className="bg05">Step 5. Click Request after checking purchase information<br />
						(Nhấn nút xác nhận sau khi kiểm tra thông tin mua hàng)</p></li>
					</ul>
				</div>

			</div>
        </div>
  );
};

export default UserGuide;
