import React from 'react';
import './StepLabel.scss'

const StepLabel = props => {
  const {step, value} = props;
  return (
    <div>
      <p className="tit_step">
        <strong className="fc_red">{step}</strong> {value}
      </p>
    </div>
  );
};

StepLabel.defaultProps = {
    step: 'Step *',
    value: 'please check it'
}

export default StepLabel;
