import React, { useState, useEffect } from 'react';
import './InputDetail.scss';

const InputDetail = props => {
  const { onRequested, errMsg, disable } = props;

  const [inputValue, setInputValue] = useState('');  // input box에 입력된 내용.
  const [errMessage, setErrMessage] = useState('');  // 에러메시지 영역에 보여질 내용.
  const [disabled, setDisabled] = useState(false);   // input box / 버튼  비활성화 여부

  useEffect(() => {
    setErrMessage(errMsg);
    setDisabled(disable);
  }, [errMsg, disable]);

  const onClick = () => {
    if(disabled) {
      return;
    }

    if(!(inputValue.trim())) {
      setErrMessage("no input value");
      return;
    }


    console.log('InputDetail onClicked!');
    console.log(window.SRCSDK_VIETNAM);
    // console.log(inputValue);
    onRequested(inputValue);
  };

  const onChange = (e) => {
    setInputValue(e.target.value);
  };

  return (
    <div>
      <div className="gray_box01 mt20">

        <div className="input_wrap01">
          <input
            disabled={disabled}
            onChange={onChange}
            type="text"
            id="inputDetail"
            className="input_type"
            placeholder={props.placeholder}
          />

          <span className="btn btn_gray_b1 line2">
            <a onClick={onClick} href={"#link"} > {props.btnValueEng} <br /> {props.btnValueViet} </a>
          </span>
        </div>
        <p className="mt10 fc_red">{errMessage}</p>
      </div>
    </div>
  );
};

InputDetail.defaultProps = {
  placeholder: '+84 1234 5678',
  btnValueEng: 'Request',
  btnValueViet: '(Xác nhận)',
};

export default InputDetail;
